import React from 'react'
import styled from 'styled-components'
import { desktopVw } from '../../styles/utils'
import { Select } from '../FormElements'

const MAX_SELECTABLE_QUANTITY = 15

const getSelectOptions = availableQuantity => {
  if (availableQuantity) {
    const max =
      availableQuantity > MAX_SELECTABLE_QUANTITY
        ? MAX_SELECTABLE_QUANTITY
        : availableQuantity

    const selectOptions = []
    // eslint-disable-next-line immutable/no-let
    for (let index = 0; index < max; index += 1) {
      selectOptions.push(index + 1)
    }
    return selectOptions
  }

  return [1]
}

const StyledSelect = styled(Select)`
  display: none;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
    background-color: white;
    height: 56px;
    width: ${desktopVw(100)};

    .Select__styled_select {
      height: 56px;
      width: ${desktopVw(100)};

      font-family: ${props => props.theme.fonts.primary};
      font-weight: 400;
      font-size: ${desktopVw(20)};
      line-height: ${desktopVw(30)};

      padding-left: ${props => {
        return props.value ? desktopVw(26) : desktopVw(14.22)
      }};
      padding-top: 0;
      padding-bottom: 0px;
    }
  }
`

const QuantitytSelect = ({ maxQuantity, updateQuantity, checkoutQuantity }) => {
  const selectOptions = getSelectOptions(maxQuantity)
  return (
    <StyledSelect
      label='QTY'
      options={selectOptions}
      onChange={updateQuantity}
      value={checkoutQuantity}
      showLabel={false}
    />
  )
}

export default QuantitytSelect
