import React from 'react'
import styled from 'styled-components'

import CarDesktop from '../assets/car-desktop.svg'
import SecurePaymentDesktop from '../assets/cart-desktop.svg'
import NoWorriesDesktop from '../assets/thumbs-up-desktop.svg'

import NoWorriesMobileIcon from '../assets/new-thumbs-up.svg'
import CarMobileIcon from '../assets/new-car.svg'

import { desktopVw, mobileVw } from '../styles/utils'

const TrustBox = styled.div`
  display: flex;
  justify-content: space-between;
  margin-top: 32px;
  padding: 0 12px;
  align-items: center;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0 30px;
    margin-top: 22px;
  }
`

const TrustBadgeContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: ${mobileVw(90)};

  @media (max-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    &:first-child {
      .trust__badges__logo_container {
        display: flex;
        align-items: center;
      }
    }
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(110)};
  }
`

const LogoContainer = styled.div`
  @media (max-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    height: 38px;

    svg {
      height: 38px;
      width: 38px;
    }

    display: flex;
    align-items: center;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    height: 24px;

    svg {
      height: 24px;
    }

    .no_worries_inner {
      margin-top: 7px;
    }
  }
`

const LogoWrapperDesktop = styled.div`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
  }
`

const LogoWrapperMobile = styled.div`
  display: block;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const TextWrapper = styled.div`
  font-size: 10px;
  font-weight: 500;
  text-align: center;
  margin-top: 7px;
`

const CarMobile = styled(CarMobileIcon)`
  width: 62px !important;
`

const SecurePaymentMobile = styled(SecurePaymentDesktop)`
  position: relative;
  right: 2px;
`

const StyledCarDesktop = styled(CarDesktop)`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    height: ${desktopVw(18.5)};
    width: ${desktopVw(32)};
  }
`

export const TrustBadges = ({ className }) => (
  <TrustBox className={className}>
    <TrustBadgeContainer className='trust__badges__trust_badge_container car-badge'>
      <LogoContainer className='trust__badges__logo_container'>
        <LogoWrapperDesktop>
          <StyledCarDesktop />
        </LogoWrapperDesktop>
        <LogoWrapperMobile>
          <CarMobile />
        </LogoWrapperMobile>
      </LogoContainer>

      <TextWrapper className='trust__badges__text_wrapper'>
        <div>Free</div>
        <div>Shipping</div>
      </TextWrapper>
    </TrustBadgeContainer>

    <TrustBadgeContainer className='trust__badges__trust_badge_container cart-badge'>
      <LogoContainer className='trust__badges__logo_container'>
        <LogoWrapperDesktop>
          <SecurePaymentDesktop />
        </LogoWrapperDesktop>
        <LogoWrapperMobile>
          <SecurePaymentMobile />
        </LogoWrapperMobile>
      </LogoContainer>

      <TextWrapper className='trust__badges__text_wrapper'>
        <div>Secure</div>
        <div>Payment</div>
      </TextWrapper>
    </TrustBadgeContainer>

    <TrustBadgeContainer className='trust__badges__trust_badge_container no-worries-badge'>
      <LogoContainer className='trust__badges__logo_container'>
        <LogoWrapperDesktop>
          <NoWorriesDesktop />
        </LogoWrapperDesktop>
        <LogoWrapperMobile>
          <NoWorriesMobileIcon />
        </LogoWrapperMobile>
      </LogoContainer>

      <TextWrapper className='trust__badges__text_wrapper'>
        <div>365 Day</div>
        <div>Free Returns</div>
      </TextWrapper>
    </TrustBadgeContainer>
  </TrustBox>
)
