import React from 'react'
import styled from 'styled-components'

import WishlistButton from './WishlistButton'
import { desktopVw } from '../styles/utils'

const PriceContainer = styled.div`
  display: flex;
  margin-bottom: 15px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(17)};
  }
`

const PriceBar = styled.div``

const Title = styled.h1`
  font-size: 18px;
  line-height: 30px;
  font-family: ${props => props.theme.fonts.primary};
  font-weight: normal;
  margin-bottom: 8px;
  margin-top: var(--marginTopMobile);

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(18)};
    line-height: ${desktopVw(25)};
    margin-top: var(--marginTopDesktop);
    margin-right: ${desktopVw(12)};
  }
`

const PriceLabel = styled.p`
  font-size: 24px;
  font-weight: 500;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(24)};
  }
`

const PackInfo = styled.p`
  font-size: 9px;
  text-transform: lowercase;
  margin-bottom: 5px;
  margin-top: -2px;
`

const SubtitleWrapper = styled.div`
  margin-left: 31px;
  color: ${props => props.theme.colors.greyLight};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-left: ${desktopVw(33)};
  }
`

const TraditionalPrice = styled.p`
  font-size: 24px;
  text-decoration: line-through;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(24)};
  }
`

const Subtitle = styled.p`
  font-size: 11px;
  text-transform: capitalize;
  color: ${props => props.theme.colors.greyLight};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(11)};
  }
`

const DiscountPercentage = styled.p`
  font-size: 11px;
  color: ${props => props.theme.colors.red};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(11)};
  }
`

const RatingContainer = styled.div`
  position: relative;
  bottom: 2px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(10)};
    position: relative;
    bottom: 3px;
  }
`

const RatingsSection = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;
  margin-top: 20px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(4)};
    margin-top: 0;
  }
`

const StyledWishButton = styled(WishlistButton)`
  & {
    svg {
      position: relative;
      height: 18px;
      right: 5px;
      top: 0px;
    }
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    & {
      svg {
        position: relative;
        height: ${desktopVw(20)};
        right: ${desktopVw(5)};
        top: 0px;
      }
    }
  }
`

export const ProductTitle = ({
  color,
  contentfulProduct,
  handle,
  leastCompetitorPrice,
  price,
  ratingContainer,
  title,
  rating,
  size,
  productId,
  productPackVariantObj,
  selectedSize,
  trackerSection,
}) => (
  <>
    <RatingsSection>
      <RatingContainer>{ratingContainer}</RatingContainer>
      <StyledWishButton
        handle={handle}
        contentfulProduct={contentfulProduct}
        color={color}
        size={size}
        productId={productId}
        trackerSection={trackerSection}
      />
    </RatingsSection>
    <Title
      style={{
        '--marginTopMobile': rating ? 'unset' : '-35px',
        '--marginTopDesktop': rating ? 'unset' : desktopVw(-30),
      }}
    >
      {title}
    </Title>
    <PriceContainer>
      {price && (
        <PriceBar>
          <PriceLabel>{`$${price.toFixed(2)}`}</PriceLabel>
          {productPackVariantObj && !selectedSize && (
            <PackInfo>{`per ${productPackVariantObj?.packType}`}</PackInfo>
          )}
          {leastCompetitorPrice && (
            <DiscountPercentage>
              You Save:{' '}
              {Math.round(
                ((parseInt(leastCompetitorPrice) - price) /
                  parseInt(leastCompetitorPrice)) *
                  100,
              )}
              %
            </DiscountPercentage>
          )}
        </PriceBar>
      )}
      {leastCompetitorPrice && (
        <SubtitleWrapper>
          <TraditionalPrice>{`$${leastCompetitorPrice.toFixed(
            2,
          )}`}</TraditionalPrice>
          {productPackVariantObj && !selectedSize && (
            <PackInfo>{`per ${productPackVariantObj?.packType}`}</PackInfo>
          )}
          <Subtitle>Traditional Retail</Subtitle>
        </SubtitleWrapper>
      )}
    </PriceContainer>
  </>
)
