import React, { useState, useEffect } from 'react'
import styled from 'styled-components'

import SubscribeForm from './SubscribeForm'

import { desktopVw } from '../styles/utils'
import { isValidEmail } from '../utils'

const StyledSubscribeForm = styled(SubscribeForm)`
  .subscribeform {
    &__input,
    &__submit {
      height: 72px;
      border-color: black;
      color: black;

      &::placeholder {
        color: black;
      }
    }

    &__success-msg {
      color: black;
    }
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    .subscribeform {
      &__input,
      &__submit {
        height: ${desktopVw(72)};
        border-color: ${props => props.theme.colors.primary};
        color: ${props => props.theme.colors.primary};

        &::placeholder {
          color: ${props => props.theme.colors.primary};
        }
      }
      &__success-msg {
        color: ${props => props.theme.colors.primary};
      }
    }

    margin-bottom: ${desktopVw(20)};
  }
`

const JoinTheWaitlistForm = ({ addProductToWaitlist, successMessage }) => {
  const [errorMsg, setErrorMsg] = useState(null)

  const [showSuccessMsg, setShowSuccessMsg] = useState(false)

  const [showLoading, setShowLoading] = useState(false)

  const [email, setEmail] = useState('')

  const handleSubmit = e => {
    e.preventDefault()
    try {
      if (!isValidEmail(email)) {
        setShowSuccessMsg(false)
        setErrorMsg(`You entered an invalid e-mail address`)
        return
      }
      setShowLoading(true)
      addProductToWaitlist(email)
    } catch (error) {
      setShowLoading(false)
      console.log(error)
    }
  }

  useEffect(() => {
    setErrorMsg('')
  }, [email])

  const onFocus = () => {
    setShowLoading(false)
    setErrorMsg('')
  }

  return (
    <StyledSubscribeForm
      email={email}
      handleChange={setEmail}
      onFocus={onFocus}
      handleSubmit={handleSubmit}
      success={showSuccessMsg}
      successMessage={successMessage}
      errorMsg={errorMsg}
      submitText='Join'
      loadingMsg={showLoading ? 'Adding' : null}
    />
  )
}

export default JoinTheWaitlistForm
