import React, { useEffect, useState } from 'react'
import styled from 'styled-components'

import useScrollListener from '../hooks/useScrollListener'
import { desktopVw, mobileVw } from '../styles/utils'
import Button from './Button'
import { StyledTippy } from './StyledTippy'

const StickyTitle = styled.div`
  opacity: var(--opacity);
  pointer-events: var(--pointerEvents);
  background: ${props => props.theme.colors.secondary};
  position: fixed;
  z-index: 1001;
  top: 0;
  width: 100%;
  margin-left: -10px;
  transition: 0.2s;
  padding: 12px 12px 10px 12px;
  box-shadow: 0 3px 7px rgba(0, 0, 0, 0.1);
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const TitleText = styled.div`
  font-size: 18px;
  word-break: break-word;
  margin-bottom: 5px;
`

const Wrapper = styled.div`
  display: flex;
`

const PriceContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 17px;
  margin-right: 32px;
`

const QuincePrice = styled.div`
  font-size: 24px;
`

const PackInfo = styled.p`
  font-size: 9px;
  text-transform: lowercase;
  margin-bottom: 2px;
  margin-top: -2px;
`

const SavingsText = styled.div`
  font-size: 14px;
  color: #db3333;
`

const AddToCart = styled(Button)`
  background: ${props => props.theme.colors.yellow};
  padding: 10px;
  width: 100%;
  flex: 1;
  border: none;
  height: 50px;

  &:disabled {
    background: ${props => props.theme.colors.greyLighter};
    color: white;
    cursor: initial;

    &:hover {
      background: ${props => props.theme.colors.greyLighter};
    }
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    height: 56px;
    width: ${desktopVw(400)};
    font-size: ${desktopVw(14)};
    text-transform: uppercase;
    font-weight: 500;
    font-family: ${props => props.theme.fonts.heading};
    margin-bottom: 12px;
  }
`

const StyledCart = styled(AddToCart)`
  margin: 9px;
  width: ${mobileVw(207)};
  font-size: 22px;
  font-weight: 500;
  color: rgb(30, 30, 30);
  height: unset;
`

const StickyAddToCart = styled.div`
  display: flex;
  opacity: var(--opacity);
  pointer-events: var(--pointerEvents);
  background: ${props => props.theme.colors.secondary};
  position: fixed;
  z-index: 1001;
  bottom: 0;
  width: 100%;
  margin-left: -10px;
  transition: 0.2s;
  height: 65px;
  box-shadow: 0 -3px 7px rgba(0, 0, 0, 0.1);
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

export const StickySection = ({
  onClick,
  onShow,
  preorderInfo,
  price,
  ratingContainer,
  s,
  title,
  visible,
  addToCartRef,
  outOfStock,
  productPackVariantObj,
  selectedSize,
}) => {
  const [isScrolling] = useScrollListener()
  const [isHeaderSticky, setHeaderSticky] = useState(false)
  const [isFootSticky, setFootSticky] = useState(false)
  const [lastScrollPosition, setLastScrollPosition] = useState(0)

  useEffect(() => {
    if (typeof window !== 'undefined') {
      if (!(addToCartRef && addToCartRef.current)) return
      const buttonTop = addToCartRef.current.getBoundingClientRect().top
      const scrolledPastButton = buttonTop < 40
      if (scrolledPastButton && !isFootSticky) {
        setFootSticky(true)
      } else if (!scrolledPastButton && isFootSticky) {
        setFootSticky(false)
      }
      if (window.pageYOffset >= lastScrollPosition) {
        setLastScrollPosition(window.pageYOffset)
        if (scrolledPastButton && !isHeaderSticky) {
          setHeaderSticky(true)
        } else if (!scrolledPastButton && isHeaderSticky) {
          setHeaderSticky(false)
        }
      } else {
        setLastScrollPosition(window.pageYOffset)
        if (isHeaderSticky) {
          setHeaderSticky(false)
        }
      }
    }
  }, [isScrolling])

  return (
    <>
      <StickyTitle
        style={{
          '--opacity': isHeaderSticky ? 1 : 0,
          '--pointerEvents': isHeaderSticky ? 'auto' : 'none',
        }}
      >
        <TitleText>{title}</TitleText>
        {ratingContainer}
      </StickyTitle>
      {!outOfStock && (
        <StickyAddToCart
          style={{
            '--opacity': isFootSticky ? 1 : 0,
            '--pointerEvents': isFootSticky ? 'auto' : 'none',
          }}
        >
          <StyledTippy
            content='Please select a size'
            visible={visible && isFootSticky}
            animation='scale'
            onShow={onShow}
          >
            <Wrapper>
              {price && (
                <PriceContainer>
                  <QuincePrice>${price.toFixed(2)}</QuincePrice>
                  {productPackVariantObj && !selectedSize && (
                    <PackInfo>{`per ${productPackVariantObj?.packType}`}</PackInfo>
                  )}
                  <SavingsText>
                    You Save:{' '}
                    {Math.round(((parseInt(s) - price) / parseInt(s)) * 100)}%
                  </SavingsText>
                </PriceContainer>
              )}
              <StyledCart onClick={onClick}>
                {preorderInfo ? 'Pre-Order' : 'Add To Cart'}
              </StyledCart>
            </Wrapper>
          </StyledTippy>
        </StickyAddToCart>
      )}
    </>
  )
}
