/* eslint-disable immutable/no-mutation */
import React, { useEffect } from 'react'
import styled from 'styled-components'

const StyledPaymentProviders = styled.div`
  padding: 0 10px 0;
  justify-content: flex-end;
  text-transform: uppercase;
  font-size: 10px;
  text-align: center;
  color: #0b090a;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    color: ${props => props.theme.colors.greyLighter};
    font-size: 12px;
  }
`

const SezzleContainer = styled.div`
  text-transform: none;
  font-size: 10px;
  color: ${props => props.theme.colors.lightCharcoalGrey} !important;

  .sezzle-checkout-button-wrapper .sezzle-button-text .sezzle-payment-amount {
    font-size: 11px !important;
    font-weight: 400 !important;
    line-height: 18px !important;
  }
`

const Price = styled.span`
  display: none;
`

const installmentPrice = price => {
  return parseFloat(price / 4).toFixed(2)
}

const PaymentProviders = ({ quincePrice, className }) => {
  useEffect(() => {
    document.sezzleConfig = {
      targetXPath: '.amount',
      renderToPath: '..',
      fontSize: 11,
      fontWeight: 400,
      lineHeight: '18px',
      marginTop: '7px',
      parseMode: 'period',
      altVersionTemplate: {
        en: quincePrice
          ? `Pay in 4 installments of $${installmentPrice(
              quincePrice,
            )} with  %%logo%% %%info%%`
          : `Pay in 4 installments with  %%logo%% %%info%%`,
      },
    }
    const script = document.createElement('script')
    script.src = `https://widget.sezzle.com/v1/javascript/price-widget?uuid=${process.env.GATSBY_SEZZLE_MERCHANT_ID}`
    script.async = true
    document.body.appendChild(script)
  }, [])

  return (
    <StyledPaymentProviders className={className}>
      <SezzleContainer>
        <span className='price'>
          <Price className='amount'>${quincePrice || 0}</Price>
        </span>
      </SezzleContainer>
    </StyledPaymentProviders>
  )
}

export default PaymentProviders
