import React from 'react'
import styled from 'styled-components'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import { BLOCKS, MARKS } from '@contentful/rich-text-types'
import OfferIcon from '../../assets/offer_icon.svg'

import { desktopVw, desktopVh } from '../../styles/utils'

const StyledOfferMessageSection = styled.section`
    text-align: center;
    font-weight: bold !important;
    color: ${props => props.theme.colors.primary};
    font-family: ${props => props.theme.fonts.primary};
    font-style: normal;
    font-weight: 500 !important;
    background-color: ${props => props.theme.colors.lightPink};
    display: flex;
    justify-content: center;

    font-size: 11px;
    line-height: 14px;

    margin-left: -10px;
    margin-right: -10px;

    padding: 15px 16px 16px 17px;
    
    .green {
      color: ${props => props.theme.colors.green};
    }
    
    .red {
      color: ${props => props.theme.colors.red};
    }
    
    .strikeout {
      text-decoration: line-through;
    }

    a {
      text-decoration: underline;
    }

    @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
      text-align: center;
      font-size: ${desktopVw(11)};
      line-height: ${desktopVw(14)};
      margin: ${desktopVh(18)} -${desktopVw(40)} ${desktopVh(10)}; 
      padding: 21px ${desktopVw(38)} 23px ${desktopVw(38)};      
    }
}
`

const BoldText = styled.span`
  font-weight: 500;
`

const options = {
  renderNode: {
    [BLOCKS.PARAGRAPH]: (node, children) => {
      return children.map(child => {
        if (typeof child === 'string') {
          return <span dangerouslySetInnerHTML={{ __html: child }} />
        }
        return child
      })
    },
  },
  renderMark: {
    [MARKS.BOLD]: text => {
      return <BoldText dangerouslySetInnerHTML={{ __html: text }} />
    },
  },
}

const OfferIconContainer = styled.div`
  width: 20.5px;
  margin-right: 4.5px;

  svg {
    width: 20.5px;
    position: relative;
    bottom: 4px;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(28)};
    margin-right: ${desktopVw(7)};

    svg {
      width: ${desktopVw(28)};
      position: relative;
      bottom: 0px;
    }
  }
`

const OfferMessageContainer = styled.div`
  flex-direction: column;
  display: flex;
  justify-content: center;
`

const OfferMessage = ({ json }) => {
  return (
    <StyledOfferMessageSection>
      <OfferIconContainer>
        <OfferIcon />
      </OfferIconContainer>

      <OfferMessageContainer>
        {documentToReactComponents(json, options)}
      </OfferMessageContainer>
    </StyledOfferMessageSection>
  )
}

export default OfferMessage
