import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import Image from 'gatsby-image'

// Styles
import { desktopVw, mobileVw } from '../styles/utils'

// Utils
import {
  getHyphenatedColorName,
  getPathName,
  getPathParams,
  getProductCarousel,
  getProductImagesOnSizeAndColor,
} from '../utils'

// Components
import Modal from './Modal'
import MenuBar from './Header/MenuBar'
import ProductCarousel from './ProductCarousel'
import ProductForm from './ProductForm'
import TextButton from './TextButton'

const StyledQuickShopModal = styled(Modal)`
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  overflow-y: scroll;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    flex-direction: row;
    height: auto;
    width: auto;
  }
`

const CarouselOverlay = styled.div`
  position: relative;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const StyledMenuBar = styled(MenuBar)`
  z-index: 1000;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  background: transparent;

  svg {
    fill: ${props => props.theme.colors.primary};
  }

  a,
  button {
    color: ${props => props.theme.colors.primary};
  }
`

const ImageWrapper = styled.div`
  display: block;
  position: relative;
  flex-shrink: 0;
  height: ${mobileVw(413)};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
    background: ${props => props.theme.colors.greyLighter};
    width: ${desktopVw(400)};
    height: auto;
  }
`

const Body = styled.div`
  flex: 1;
  padding: 0 10px;

  @media (max-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: 95px;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(35)} ${desktopVw(40)};
    max-height: 90vh;
    overflow-y: scroll;
  }
`

const ButtonWrapper = styled.div`
  display: flex;
  justify-content: center;
  margin: 26px 0 22px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: ${desktopVw(42)} 0 ${desktopVw(7)};
  }
`

const StyledTextButton = styled(TextButton)`
  text-transform: uppercase;
  font-size: 12px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
  }
`

const StyledProductForm = styled(ProductForm)`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    & {
      .product__form__add_to_cart_container {
        background: ${props => props.theme.colors.whiteSmoke};
        padding-top: 30px;
        border-bottom: none;
      }

      width: ${desktopVw(420)};

      .product__form__desktop_trust_badges {
        display: flex;
      }
    }
  }
`

const QuickShopModal = ({
  product,
  contentfulProduct,
  shopifyProduct,
  reviewSummary,
  title,
  variants,
  sizes,
  startingColor,
  open,
  onClose,
  tracker,
  shopifyColors,
  showMenuBar = true,
  showCloseBtn = false,
  className,
  productPackVariantMap,
  addToCartTrackerSection,
}) => {
  const { slug, colors: colorProps } = contentfulProduct

  const productImages = getProductCarousel(contentfulProduct)

  const colors = colorProps.filter(
    cObj =>
      productImages.findIndex(imageObj => imageObj.color.name === cObj.name) >
      -1,
  )

  const [carouselImages, setCarouselImages] = useState([])
  const [selectedColor, setSelectedColor] = useState(startingColor)
  const [selectedSize, setSelectedSize] = useState(null)
  const [linkParams, setLinkParams] = useState({
    tracker: tracker ? `${tracker}__quick_shop` : undefined,
  })

  useEffect(() => {
    if (productImages && productImages[0]) {
      setCarouselImages(productImages[0].images)
    }
  }, [productImages])

  useEffect(() => {
    if (productImages && selectedColor) {
      const newCarouselImages = getProductImagesOnSizeAndColor(
        productImages,
        selectedSize,
        selectedColor,
      )

      if (newCarouselImages) {
        setCarouselImages(newCarouselImages.images)
      }
    }
  }, [productImages, selectedColor, startingColor, selectedSize])

  useEffect(() => {
    if (selectedColor) {
      linkParams.color = getHyphenatedColorName(selectedColor)
      setLinkParams(linkParams)
    }
  }, [selectedColor])

  return (
    <StyledQuickShopModal
      open={open}
      onClose={onClose}
      showCloseBtn={showCloseBtn}
      className={className}
    >
      <CarouselOverlay>
        {showMenuBar && <StyledMenuBar onClose={onClose} />}
        <ProductCarousel images={carouselImages && carouselImages} />
      </CarouselOverlay>
      <ImageWrapper>
        {carouselImages.length > 0 && (
          <Image
            style={{
              position: 'absolute',
              left: '0',
              right: '0',
              bottom: '0',
              top: '0',
            }}
            fluid={carouselImages[0].fluid}
            alt={carouselImages[0].description || carouselImages[0].title}
          />
        )}
      </ImageWrapper>
      <Body>
        <StyledProductForm
          product={product}
          contentfulProduct={{ ...contentfulProduct, colors }}
          shopifyProduct={shopifyProduct}
          variants={variants}
          title={title}
          sizes={sizes}
          reviewSummary={reviewSummary}
          startingColor={startingColor}
          setSelectedColor={setSelectedColor}
          trackerSection={
            addToCartTrackerSection
              ? `${addToCartTrackerSection}__quick_shop`
              : 'quick_shop'
          }
          shopifyColors={shopifyColors}
          productPackVariantMap={productPackVariantMap}
          setSelectedSize={setSelectedSize}
        />
        <ButtonWrapper>
          <StyledTextButton to={getPathName(slug) + getPathParams(linkParams)}>
            View full product details
          </StyledTextButton>
        </ButtonWrapper>
      </Body>
    </StyledQuickShopModal>
  )
}

export default QuickShopModal
