import React from 'react'
import styled from 'styled-components'
import dayjs from 'dayjs'

import { desktopVw } from '../styles/utils'

const PreOrderMessageWrapper = styled.div`
  font-size: 12px;
  border-top: 1px solid ${props => props.theme.colors.greyLighter};
  border-bottom: 1px solid ${props => props.theme.colors.greyLighter};
  text-align: center;
  padding: 10px 0;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-top: ${desktopVw(12)};
    padding: ${desktopVw(10)} 0;
    font-size: ${desktopVw(12)};
  }
`

const commonMessage = 'Pre order yours now'
const comingSoonDescription = [
  'This new style is coming soon on',
  commonMessage,
]
const backInStockDescription = [
  'This style will be back in stock on',
  commonMessage,
]

const PreOrderMessageViewer = ({ preorderInformation }) => {
  const formattedDate = dayjs(preorderInformation.date).format('MM/DD/YYYY')
  const description =
    preorderInformation.comingSoon === true
      ? comingSoonDescription
      : backInStockDescription
  return (
    <PreOrderMessageWrapper>
      {description.map((descriptionLine, index) => (
        <div>
          {' '}
          {descriptionLine + (index === 0 ? ' ' + formattedDate : '')}{' '}
        </div>
      ))}
    </PreOrderMessageWrapper>
  )
}

export default PreOrderMessageViewer
