import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import Tippy from '@tippyjs/react'
import 'tippy.js/dist/tippy.css'

import SizeGuideModal from './SizeGuideModal'
import TextButton from './TextButton'

import { desktopVw } from '../styles/utils'
import {
  decodeIdFromGraphQLResponse,
  getRequiredVariant,
  showSizesWithPrice,
} from '../utils'
import { colors } from '../styles/theme'
import RingSizeGuideModal from './RingSizeGuideModal'
import RingSizingGuide from './RingSizingGuide.json'

const PRODUCT_SUB_TYPE_RINGS = 'Rings'

const DEFAULT_RETIRED_VARIANTS = []

const StyledSizeSelector = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  text-transform: uppercase;
  font-weight: 500;
  border-top: 1px solid ${props => props.theme.colors.greyLighter};
  padding-top: 10px;
  margin-bottom: 14px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    align-items: flex-start;
    flex-wrap: nowrap;
    align-items: baseline;
  }
`

const Title = styled.label`
  font-size: 11px;
  margin-right: 20px;
  font-weight: 500;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
    margin: 0 ${desktopVw(14)} 0 0;
    font-size: ${desktopVw(11)};
  }
`

const InventoryStatusMsg = styled.div`
  color: #b32c0a;
  font-size: 11px;
  font-weight: 500;
  text-transform: uppercase;
`

const Wrapper = styled.div`
  display: grid;
  grid-template-columns: var(--gridTemplateColumnsMobile);
  column-gap: var(--columnGap);
  row-gap: var(--rowGap);
  flex-wrap: wrap;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0;
  }
  @media (min-width: ${props =>
      props.theme.breakpoints.tablet}px) and (max-width: 1260px) {
    grid-template-columns: var(--gridTemplateColumnsTablet);
  }
  @media (min-width: ${props => props.theme.breakpoints.desktop}px) {
    grid-template-columns: var(--gridTemplateColumnsDesktop);
  }
`

const Option = styled.div`
  position: relative;
  height: var(--height);

  input {
    &:checked {
      + label {
        border-color: ${props => props.theme.colors.primary};
        font-weight: 500;
      }
    }
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0;

    &:first-of-type {
      margin: 0;

      input {
        &:checked {
          + label {
            margin-left: 0;
          }
        }
      }
    }
  }
`

const Label = styled.label`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  white-space: nowrap;
  border: var(--border);
  width: var(--width);
  height: var(--height);
  padding: var(--padding);
  border-radius: 6px;
  transition: width 150ms ease-out, height 150ms ease-out;
  font-weight: normal;
  font-size: 11px;
`

const Name = styled.div`
  font-size: 12px;
`

const PriceValue = styled.div`
  margin-top: 2px;
  font-size: 10px;
`

const SizeChartButton = styled(TextButton)`
  margin: 0 1px 0 0;
  font-size: 10px;
  line-height: 24px;
  text-decoration: underline;
  text-align: right;
  background-position: 0 75%;
  background-image: linear-gradient(#979797, #979797);
  white-space: nowrap;
  font-weight: 500;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: initial;
    margin: 0;
    font-size: ${desktopVw(10)};
    line-height: ${desktopVw(24)};
    height: ${desktopVw(23)};
  }
`

const Input = styled.input`
  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
  opacity: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
`

const StyledTippy = styled(Tippy)`
  background: none;
  color: ${props => props.theme.colors.red};
  text-transform: uppercase;
  font-size: ${desktopVw(10)};

  &[data-placement^='top'] > .tippy-arrow::before {
    border-top-color: ${props => props.theme.colors.red};
    font-size: ${desktopVw(10)};
  }

  .tippy-content {
    margin-left: ${desktopVw(48)};
    padding-bottom: ${desktopVw(1)};
    margin-bottom: ${desktopVw(-2)};
  }

  @media (max-width: ${props => props.theme.breakpoints.tablet}px) {
    display: none;
  }
`

const StyledPackInfo = styled.div`
  display: flex;
  align-items: center;
`

const PackSize = styled.p`
  font-size: 29px;
  margin-right: 3px;
`

const PackDetail = styled.p``

const TotalPrice = styled.p`
  font-size: 12px;
`

const UnitPrice = styled.p`
  font-size: 9px;
  text-transform: lowercase;
`

function NormalInfo({ hasSamePrice, price, showPrice, value }) {
  return (
    <>
      <Name>{value}</Name>
      {showPrice && !hasSamePrice && <PriceValue>${price}</PriceValue>}
    </>
  )
}

function PackInfo({ price, productPackVariantObj }) {
  return (
    <StyledPackInfo>
      <PackSize>{productPackVariantObj?.packQuantity}</PackSize>
      <PackDetail>
        {price && <TotalPrice>{`Pack $${price}`}</TotalPrice>}
        {price && (
          <UnitPrice>
            (
            {`$${(price / productPackVariantObj?.packQuantity).toFixed(2)} / ${
              productPackVariantObj?.packType
            }`}
            )
          </UnitPrice>
        )}
      </PackDetail>
    </StyledPackInfo>
  )
}

const ProductSizeSelector = ({
  className,
  sizes,
  onChange,
  selected,
  selectedColor,
  variants,
  sizeGuide,
  sizeGuideJson,
  tippyVisible,
  hideTippyVisible,
  isOutOfStock = false,
  productSubtype,
  productPackVariantMap = {},
  selectedVariantId,
  retiredVariants = DEFAULT_RETIRED_VARIANTS,
  productId,
}) => {
  const [sizeSelection, setSizeSelection] = useState(
    sizes?.map(size => ({ value: size })) ?? [],
  )
  const [activeSizeGuide, setActiveSizeGuide] = useState(false)

  const showPrice = sizes ? showSizesWithPrice(sizes) : false

  const hideTippy = instance => {
    if (hideTippyVisible) {
      setTimeout(hideTippyVisible, 2000)
    }
  }

  useEffect(() => {
    if (!showPrice) {
      const filteredSizes = []

      const activeSelectedColorVariants = (variants ?? []).filter(
        colorVariant => {
          const colorVariantID = colorVariant.node.id
            ? decodeIdFromGraphQLResponse(colorVariant.node.id)
            : undefined
          return !retiredVariants.find(
            it => it.shopifyVariantId === Number(colorVariantID),
          )
        },
      )

      activeSelectedColorVariants.forEach(selectedVariant => {
        const variantSize = selectedVariant.node.selectedOptions.find(
          ({ name }) => name === 'Size',
        )
        const variantColor = selectedVariant.node.selectedOptions.find(
          ({ name }) => name === 'Color',
        )
        if (
          sizes.includes(variantSize?.value) &&
          selectedColor === variantColor?.value
        )
          filteredSizes.push(variantSize)
      })

      setSizeSelection(filteredSizes)
      return
    }

    if (!variants) return

    const colorIndex = variants[0].node.selectedOptions.findIndex(
      ({ name }) => name === 'Color',
    )

    const sizeIndex = variants[0].node.selectedOptions.findIndex(
      ({ name }) => name === 'Size',
    )

    const selectedColorVariants = variants
      .filter(
        ({ node }) =>
          node.selectedOptions[colorIndex].value.trim() ===
          selectedColor.trim(),
      )
      .filter(({ node }) => !node.title.includes('Group'))

    // removing Navy/Xs/S variant for organic ankle socks
    // this is just a temporary solution
    const activeSelectedColorVariants = selectedColorVariants.filter(
      colorVariant => {
        const colorVariantID = colorVariant.node.id
          ? decodeIdFromGraphQLResponse(colorVariant.node.id)
          : undefined
        return !retiredVariants.find(
          it => it.shopifyVariantId == colorVariantID,
        )
      },
    )

    const availableColorSelectedVariants = activeSelectedColorVariants.filter(
      selectedVariant => {
        const size = selectedVariant.node.selectedOptions.find(
          ({ name }) => name === 'Size',
        )
        return sizes.includes(size?.value)
      },
    )
    const selectionWithPrice = availableColorSelectedVariants.map(
      ({ node }) => ({
        value: node.selectedOptions[sizeIndex].value,
        price: Number(node.priceV2.amount).toFixed(2),
      }),
    )

    setSizeSelection(selectionWithPrice)
    if (selectionWithPrice.length === 1) {
      onChange({
        target: {
          name: 'Size',
          value: selectionWithPrice[0]?.value,
        },
      })
    }
  }, [
    selectedColor,
    variants,
    showPrice,
    sizes,
    retiredVariants,
    selectedVariantId,
  ])

  const hasSamePrice =
    sizeSelection && sizeSelection.length > 0
      ? sizeSelection.every(({ price }) => price === sizeSelection[0].price)
      : false

  const isProductSubtypeRings = productSubtype === PRODUCT_SUB_TYPE_RINGS

  const showSizes = sizeSelectOption => {
    if (sizeSelectOption) {
      // check for 'One Size', 'one size', 'default', 'Default'
      if (sizeSelectOption.length === 1) {
        if (
          (sizeSelectOption[0]?.value || '').toLowerCase() === 'one size' ||
          (sizeSelectOption[0]?.value || '').toLowerCase() === 'default'
        )
          return false
      }
      return sizeSelectOption.length > 0
    }
  }

  return (
    <>
      <StyledSizeSelector className={className} showPrice={showPrice}>
        {showSizes(sizeSelection) ? (
          <>
            <StyledTippy
              content='Please select a size'
              visible={tippyVisible}
              animation='scale'
              onShow={instance => hideTippy(instance)}
            >
              <Title>
                {productPackVariantMap[selectedVariantId] ? 'Packs' : 'Size'}
              </Title>
            </StyledTippy>
          </>
        ) : (
          <Title>One Size</Title>
        )}
        {isOutOfStock && <InventoryStatusMsg>Sold Out</InventoryStatusMsg>}
        {(sizeGuide ||
          (sizeGuideJson && Object.keys(sizeGuideJson).length > 0) ||
          isProductSubtypeRings) && (
          <>
            <SizeChartButton
              type='button'
              aria-label='Toggle size guide'
              onClick={() => setActiveSizeGuide(true)}
              trackParams={{
                trackGA: true,
                trackInternal: true,
                category: 'education',
                action: 'view_size_chart',
              }}
            >
              Size Chart
            </SizeChartButton>

            {!isProductSubtypeRings && (
              <SizeGuideModal
                open={activeSizeGuide}
                closeModal={() => setActiveSizeGuide(false)}
                sizeGuide={sizeGuide}
                sizeGuideJson={sizeGuideJson}
              />
            )}

            {isProductSubtypeRings && (
              <RingSizeGuideModal
                open={activeSizeGuide}
                closeModal={() => setActiveSizeGuide(false)}
                sizeGuideJson={RingSizingGuide}
              />
            )}
          </>
        )}
      </StyledSizeSelector>
      <Wrapper
        style={{
          '--gridTemplateColumnsMobile': showPrice
            ? 'repeat(3, 1fr)'
            : 'repeat(9, 1fr)',
          '--gridTemplateColumnsTablet': showPrice
            ? 'repeat(3, 1fr)'
            : 'repeat(7, 1fr)',
          '--gridTemplateColumnsDesktop': showPrice
            ? 'repeat(3, 1fr)'
            : 'repeat(10, 1fr)',
          '--columnGap': showPrice ? '18px' : '10px',
          '--rowGap': showPrice ? '15px' : '10px',
        }}
      >
        {showSizes(sizeSelection) &&
          sizeSelection.map(({ value, price }, i) => {
            const variantObj = getRequiredVariant(
              { variants },
              selectedColor,
              value,
            )
            const variantId = variantObj
              ? decodeIdFromGraphQLResponse(variantObj?.id)
              : undefined

            return (
              <Option
                key={value + i}
                style={{ '--height': showPrice ? '49px' : 'initial' }}
              >
                <Input
                  name='Size'
                  id={value}
                  value={value}
                  checked={value === selected}
                  type='radio'
                  onChange={e => onChange(e)}
                />
                <Label
                  htmlFor={value}
                  style={{
                    '--border': showSizes(sizeSelection)
                      ? `1px solid ${colors.greyLighter}`
                      : 'none',
                    '--width': showPrice ? 'auto' : '30px',
                    '--height': showPrice ? '100%' : '30px',
                    '--padding':
                      variantId && productPackVariantMap[variantId] && '0 11px',
                  }}
                >
                  {variantId && productPackVariantMap[variantId] ? (
                    <PackInfo
                      productPackVariantObj={productPackVariantMap[variantId]}
                      price={price}
                    />
                  ) : (
                    <NormalInfo
                      value={value}
                      showPrice={showPrice}
                      hasSamePrice={hasSamePrice}
                      price={price}
                    />
                  )}
                </Label>
              </Option>
            )
          })}
      </Wrapper>
    </>
  )
}

export default ProductSizeSelector
