import * as types from './types'

/**
 * The product handle
 * @param {String} payload
 */
export const addToWaitlist = payload => ({
  type: types.ADD_TO_WAITLIST,
  payload,
})

/**
 * The product handle
 * @param {String} payload
 */
export const removeFromWaitList = payload => ({
  type: types.REMOVE_FROM_WAITLIST,
  payload,
})
