import React, { Component } from 'react'
import { stick_in_parent } from '../utils/stickyKit'
class StickyKit extends Component {
  componentDidMount() {
    if (this.props.inferParent || this.props.parentHeight > 0) {
      this.initializePlugin()
    }
  }

  componentWillUnmount() {
    this.detachPlugin()
  }

  componentDidUpdate(prevProps) {
    if (prevProps.parentHeight !== this.props.parentHeight) {
      if (prevProps.parentHeight < 1) {
        this.initializePlugin()
      } else {
        this.recalcPlugin()
      }
    }
  }

  initializePlugin() {
    if (this.plugin) {
      this.detachPlugin()
    }

    function getOffsetTop() {
      if (this.props.offsetTop) {
        if (this.props.offsetTop.includes('vw')) {
          return (Number(this.props.offsetTop.split('vw')[0]) * 1440) / 100
        }
        return this.props.offsetTop
      }
    }

    if (this.props.inferParent) {
      this.plugin = stick_in_parent(
        {
          offset_top: getOffsetTop.call(this),
          spacer: this.spacer,
          parent: this.spacer.parentNode,
        },
        [this.element],
      )
    } else {
      this.plugin = stick_in_parent(
        {
          parent: this.container,
          offset_top: getOffsetTop.call(this),
        },
        [this.element],
      )
    }
  }

  recalcPlugin() {
    const recalcEvent = new CustomEvent('sticky_kit:recalc')
    document.body.dispatchEvent(recalcEvent)
  }

  detachPlugin() {
    const recalcEvent = new CustomEvent('sticky_kit:detach')
    this.element.dispatchEvent(recalcEvent)
  }

  render() {
    if (this.props.inferParent) {
      const spacerHeight = this.element ? this.element.scrollHeight : null

      return (
        <div
          className='sticky-kit__spacer'
          style={{ height: spacerHeight }}
          ref={spacer => (this.spacer = spacer)}
        >
          <div
            className='sticky-kit__element'
            ref={element => (this.element = element)}
            style={{ paddingBottom: this.props.paddingBottom }}
          >
            {this.props.children}
          </div>
        </div>
      )
    } else {
      return (
        <div
          className='sticky-kit__container'
          ref={container => (this.container = container)}
          style={{ height: this.props.parentHeight }}
        >
          <div
            className='sticky-kit__element'
            ref={element => (this.element = element)}
            style={{ paddingBottom: this.props.paddingBottom }}
          >
            {this.props.children}
          </div>
        </div>
      )
    }
  }
}

export default StickyKit
