import React from 'react'
import styled from 'styled-components'

import { desktopVw } from '../styles/utils'

import TextButton from './TextButton'

const StyledBreadcrumbs = styled.nav`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: ${desktopVw(38)};
    padding: 0 ${desktopVw(80)};
    color: ${props => props.theme.colors.greyLight};
  }
`
const Breadcrumb = styled.span`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(8)};
    text-transform: uppercase;
  }
`

const StyledTextButton = styled(TextButton)`
  color: ${props => props.theme.colors.greyLight};

  background-image: ${props =>
    `linear-gradient(${props.theme.colors.greyLight}, ${props.theme.colors.greyLight});`};
`

const Breadcrumbs = ({ slug }) => {
  if (!slug) return null
  const breadcrumbs = slug.split('/').filter(path => path.trim() !== '')

  return (
    <StyledBreadcrumbs>
      {breadcrumbs.map((breadcrumb, i) => {
        if (i < breadcrumbs.length - 1) {
          const firstPath = breadcrumbs[0]
          if (i === 1) {
            return (
              <Breadcrumb key={`${breadcrumb}${i}`}>
                <StyledTextButton to={`/${firstPath}/${breadcrumb}`}>
                  {breadcrumb}
                </StyledTextButton>
                /
              </Breadcrumb>
            )
          }
          return (
            <Breadcrumb key={`${breadcrumb}${i}`}>
              <StyledTextButton to={`/${breadcrumb}`}>
                {breadcrumb}
              </StyledTextButton>
              /
            </Breadcrumb>
          )
        }
        return <Breadcrumb key={`${breadcrumb}${i}`}>{breadcrumb}</Breadcrumb>
      })}
    </StyledBreadcrumbs>
  )
}

export default Breadcrumbs
