import React, { useState } from 'react'
import styled from 'styled-components'

import { desktopVw } from '../styles/utils'

import Modal from './Modal'
import CloseBtnIcon from '../assets/close-button.svg'

const StyledSizeGuideModal = styled(Modal)`
  position: absolute;
  bottom: 0;
  width: 100%;
  display: flex;
  flex-direction: column;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: static;
    width: initial;
    min-width: ${desktopVw(920)};
    height: initial;
  }
`

const Container = styled.div`
  position: absolute;
  z-index: 2000;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 23px 0 20px;
  margin: auto 0 0;
  background-color: #fff;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: relative;
    padding: ${desktopVw(55)} ${desktopVw(101)} ${desktopVw(71)}
      ${desktopVw(80)};
  }
`

const Header = styled.header`
  margin: 0 0 24.3px;
  padding: 0 42px 0 21px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0 0 ${desktopVw(29)};
    padding: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
`

const Title = styled.h2`
  font-size: 22.5px;
  line-height: 27px;
  margin: 0 0 5px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(360)};
    font-size: ${desktopVw(25)};
    line-height: ${desktopVw(30)};
    margin: 0;
  }
`

const Subtitle = styled.p`
  font-size: 9px;
  line-height: 21.6px;
  text-transform: uppercase;
  color: #979797;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: relative;
    right: ${desktopVw(-20)};
    font-size: ${desktopVw(10)};
    line-height: ${desktopVw(24)};
  }
`

const CloseButton = styled.button`
  position: absolute;
  right: 21.5px;
  top: 36px;
  width: 8.5px;
  height: 8.5px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    right: ${desktopVw(32.44)};
    top: ${desktopVw(36)};
    width: ${desktopVw(11)};
    height: ${desktopVw(11)};
  }
`

const StyledCloseBtnIcon = styled(CloseBtnIcon)`
  position: absolute;
  top: -12px;
  left: -12px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    top: ${desktopVw(-12)};
    left: ${desktopVw(-12)};
  }
`

const ButtonWrapper = styled.div`
  display: flex;
  flex-direction: row;
  margin: 0 0 16.2px;
  padding: 0 0 0 20px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0 0 ${desktopVw(33)};
    padding: 0;
  }
`

const Button = styled.button`
  position: relative;
  color: ${props => (props.isActive ? '#000' : '#979797')};
  margin: 0 29px 0 0;
  font-family: ${props => props.theme.fonts.heading};
  font-size: 12.6px;
  line-height: 18px;
  /* text-transform: uppercase; */
  font-weight: 500;
  transition: color 0.33s;

  &::after {
    content: '';
    width: 100%;
    height: 1px;
    position: absolute;
    left: 0;
    bottom: 3px;
    background-color: #000;
    transform: ${props => (props.isActive ? 'scaleX(1)' : 'scaleX(0)')};
    transform-origin: 0 50%;
    transition: transform 350ms ease-out 330ms;
  }

  &:last-child {
    margin: 0;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0 ${desktopVw(17)} 0 0;
    font-size: ${desktopVw(11)};
    line-height: ${desktopVw(20)};

    &::after {
      bottom: ${desktopVw(4.5)};
    }

    &:last-child {
      margin: 0;
    }
  }
`

const Table = styled.table`
  padding: 0 0 22px 20px;
  overflow-x: scroll;
  -webkit-overflow-scrolling: touch;
  width: 100%;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    overflow-x: auto;
    padding: 0;
  }
`

const TableRow = styled.tr`
  &:first-child {
    td:not(:first-child) {
      text-transform: uppercase;
    }
  }
`

const TableItem = styled.td`
  font-size: 13.5px;
  line-height: 30.6px;
  padding: 0 30px 0 0;
  white-space: nowrap;

  &:first-child {
    padding: 0 40px 0 0;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0 ${desktopVw(60)} 0 0;

    &:first-child {
      padding: 0 ${desktopVw(77)} 0 0;
    }

    &:last-child {
      padding: 0;
    }
  }
`

const SizeGuideModal = ({ open, closeModal, sizeGuide, sizeGuideJson }) => {
  const [tab, setTab] = useState(0)

  /**
   * Size Guide Json is currently flowing from PC UI
   * TODO: Remove Size Guide after migration from contentful to PC UI
   */

  return sizeGuideJson && Object.keys(sizeGuideJson).length > 0 ? (
    <StyledSizeGuideModal open={open} onClose={closeModal} showCloseBtn={false}>
      <Container>
        <CloseButton onClick={closeModal}>
          <StyledCloseBtnIcon />
        </CloseButton>
        <Header>
          <Title>{sizeGuideJson.title}</Title>
          <Subtitle>{sizeGuideJson.information}</Subtitle>
        </Header>
        <ButtonWrapper>
          {sizeGuideJson.sections.map((section, i) => (
            <Button
              key={section.title + i}
              onClick={() => setTab(i)}
              isActive={tab === i}
            >
              {section.title}
            </Button>
          ))}
        </ButtonWrapper>
        {sizeGuideJson.sections.map((section, i) => (
          <Table
            key={section.title + i}
            style={{ display: `${tab === i ? 'block' : 'none'}` }}
          >
            <tbody>
              {section.items.map((row, i) => {
                return (
                  <TableRow key={row.key + i}>
                    <TableItem key={row.key}>{row.key}</TableItem>
                    {row.values.split(',').map((data, index) => {
                      return (
                        <TableItem key={`${data}${index}`}>{data}</TableItem>
                      )
                    })}
                  </TableRow>
                )
              })}
            </tbody>
          </Table>
        ))}
      </Container>
    </StyledSizeGuideModal>
  ) : sizeGuide ? (
    <StyledSizeGuideModal open={open} onClose={closeModal} showCloseBtn={false}>
      <Container>
        <CloseButton onClick={closeModal}>
          <StyledCloseBtnIcon />
        </CloseButton>
        <Header>
          <Title>{sizeGuide.title}</Title>
          <Subtitle>{sizeGuide.information}</Subtitle>
        </Header>
        <ButtonWrapper>
          {sizeGuide.tables.map((table, i) => (
            <Button
              key={table.title + i}
              onClick={() => setTab(i)}
              isActive={tab === i}
            >
              {table.title}
            </Button>
          ))}
        </ButtonWrapper>
        {sizeGuide.tables.map((table, i) => (
          <Table
            key={table.id}
            style={{ display: `${tab === i ? 'block' : 'none'}` }}
          >
            <tbody>
              {table.tableRows.map(row => {
                return (
                  <TableRow key={row.id}>
                    {row.tableData.map((data, index) => {
                      return (
                        <TableItem key={`${data}${index}`}>{data}</TableItem>
                      )
                    })}
                  </TableRow>
                )
              })}
            </tbody>
          </Table>
        ))}
      </Container>
    </StyledSizeGuideModal>
  ) : null
}

export default SizeGuideModal
