import React, { useEffect } from 'react'
import styled from 'styled-components'
import { useSelector, useDispatch } from 'react-redux'

import { desktopVw } from '../styles/utils'

import { toggleFromWishlist } from '../actions'

import WishlistEmpty from '../assets/wishlist-empty.svg'
import WishlistFull from '../assets/wishlist-full.svg'
import EventType from '../trackers/eventType'
import eventTracker from '../trackers/EventTracker'
import {
  decodeIdFromGraphQLResponse,
  isLoggedIn,
  getCurrentUser,
  saveState,
} from '../utils'
import useWishlist from '../hooks/useWishlist'

const Button = styled.button`
  width: 11px;
  fill: black;
  cursor: pointer;
  margin-left: auto;

  @media (min-width: ${props => props.theme.breakpoints.desktop}px) {
    width: ${desktopVw(11)};
  }
`

const InactiveWishlist = styled(WishlistEmpty)`
  fill: ${props => props.theme.colors.primary};
`

const ActiveWishlist = styled(WishlistFull)`
  fill: ${props => props.theme.colors.primary};
`

const WishlistButton = ({
  className,
  contentfulProduct,
  color,
  size = undefined,
  productId,
  trackerSection,
}) => {
  const dispatch = useDispatch()
  const { data: wishlist } = useSelector(state => state.wishlist)
  const { updateWishlist } = useWishlist()

  const wishlistId = `${contentfulProduct.shopifyProduct.handle}/${color}`

  const isActive = wishlist.some(obj => obj.wishlistId === wishlistId)

  const wishlistProduct = {
    wishlistId,
    color,
    ...contentfulProduct,
    size,
    productId,
  }

  const handleWishListButtonClick = () => {
    let lineItems = wishlist.map(product => {
      const productId = product.productId || product?.shopifyProduct?.product_id
      const decodedId = productId
        ? decodeIdFromGraphQLResponse(productId)
        : undefined
      return {
        price: product.seoPrice,
        color: product.color,
        productId: decodedId,
        size: product.size,
        title:
          product.title || product.seoTitle || product?.shopifyProduct?.title,
      }
    })

    const decodedId = decodeIdFromGraphQLResponse(productId)

    lineItems = isActive
      ? lineItems.filter(
          item =>
            item.productId !== decodedId ||
            (item.productId === decodedId && item.color !== color),
        )
      : [
          ...lineItems,
          {
            productId: decodedId,
            color: wishlistProduct.color,
            title: wishlistProduct.title,
            ...(size && { size }),
          },
        ]

    if (isLoggedIn()) {
      const userEmail = getCurrentUser().email
      updateWishlist(userEmail, wishlistProduct)
    } else {
      dispatch(toggleFromWishlist(wishlistProduct))
    }

    const eventType = isActive
      ? EventType.REMOVE_FROM_WISHLIST
      : EventType.ADD_TO_WISHLIST

    eventTracker.track(eventType, {
      productId: decodedId,
      color: wishlistProduct.color,
      ...(size && { size }),
      ...(lineItems.length > 0 && { lineItems }),
      trackerSection,
    })
  }

  return (
    <Button
      onClick={handleWishListButtonClick}
      className={className}
      aria-label={`add ${contentfulProduct?.shopifyProduct?.handle} of color ${color} to wishlist`}
    >
      {isActive ? <ActiveWishlist /> : <InactiveWishlist />}
    </Button>
  )
}

export default WishlistButton
