import { captureException } from '../components/Sentry/utils'

const shopifyProductsEndpoint = process.env.GATSBY_SHOPIFY_PRODUCTS_ENDPOINT

const useWaitlist = () => {
  const postWaitlist = async params => {
    const options = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
    }
    const urlParams = `?productId=${params.productId}&email=${params.email}`

    try {
      const response = await fetch(
        `${shopifyProductsEndpoint}/${params.productId}/waitlist${urlParams}`,
        options,
      )
      return { response, error: null, loading: false }
    } catch (error) {
      captureException(
        new Error(
          `Could not add to waitlist: Product: ${params.productId}, E-mail: ${params.email}`,
        ),
      )
      return { response: null, error, loading: false }
    }
  }

  return { postWaitlist }
}

export default useWaitlist
