import React, { useContext, useEffect, useRef, useState } from 'react'
import styled from 'styled-components'
import Image from 'gatsby-image'

import theme from '../styles/theme'

import Chevron from '../assets/chevron.svg'

import ScaledImage from './ScaledImage'
import { desktopVw, desktopVwInt } from '../styles/utils'
import ProductCarouselSlide from './ProductCarouselSlide'
import Breadcrumbs from './Breadcrumbs'
import ScaledVideo from './Video'

import StickyKit from './StickyKit'
import EventType from '../trackers/eventType'
import eventTracker from '../trackers/EventTracker'
import VideoPlayBack from '../assets/video-playback.svg'
import DesktopTrustBadges, {
  TRUSTED_BADGES_HEIGHT_VALUE,
} from './DesktopTrustBadges'
import useMounted from '../hooks/useMounted'
import { ProductContext } from '../contexts'
import {
  getEagerLoadCarouselClassName,
  getFirstNProductColorImages,
} from '../utils'

const offset = 10
const imageHeight = 553

const SidePanel = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0 ${desktopVw(80)} 0 ${desktopVw(37)};
    width: ${desktopVw(491)};
  }
`

const SidePanelDesktop = styled.div`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
  }
`

const StyledProductCarouselSlide = styled(ProductCarouselSlide)`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    height: ${props =>
      props.index === 1
        ? desktopVw(TRUSTED_BADGES_HEIGHT_VALUE)
        : desktopVw(543)};
    display: ${props => props.display};

    .product__carousel__slide__styled_slide {
      height: ${props =>
        props.index === 1
          ? desktopVw(TRUSTED_BADGES_HEIGHT_VALUE)
          : desktopVw(543)};
    }
  }
`

const VideoContainer = styled.div`
  position: relative;
  height: 526px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(434)};
    height: ${desktopVw(543)};
  }
`
const PlayButton = styled.span`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
`

const ProductCarousel = ({
  images,
  productId,
  children,
  imageNamePrefix,
  imageToColorMap,
  selectedColor,
}) => {
  const isSSR = typeof window === 'undefined'

  const [indexInView, setIndexInView] = useState(0)
  const [containerHeight, setContainerHeight] = useState(1440)
  const containerRef = useRef(null)
  const sidePanelRef = useRef(null)
  const slidesRef = useRef([])
  const thumbWrap = useRef([])
  const thumbsRef = useRef([])
  const { mounted } = useMounted()
  const { slug, productImages } = useContext(ProductContext)

  const [carouselImages, setCarouselImages] = useState(
    getFirstNProductColorImages(productImages, 1),
  )

  const setParentHeight = () => {
    const newParentHeight =
      images.length * desktopVwInt(imageHeight) * (window.innerWidth / 100) +
      offset +
      desktopVwInt(TRUSTED_BADGES_HEIGHT_VALUE) * (window.innerWidth / 100)
    if (typeof window !== 'undefined') {
      if (sidePanelRef.current) {
        if (sidePanelRef.current.scrollHeight >= newParentHeight)
          setContainerHeight('100%')
        else setContainerHeight(newParentHeight)
      }
    }
  }

  useEffect(() => {
    setCarouselImages(images)
  }, [images])

  useEffect(() => {
    setParentHeight()
  }, [children])

  useEffect(() => {
    slidesRef.current = slidesRef.current.slice(0, images.length)
    thumbsRef.current = thumbsRef.current.slice(0, images.length)
    // when images are loaded re-calculate container height
    setParentHeight()
  }, [images])

  const scrollTo = i => {
    if (slidesRef.current.length > 0) {
      window.scrollTo({
        top:
          slidesRef.current[i].getBoundingClientRect().top +
          window.scrollY -
          theme.header.desktop.height,
        behavior: 'smooth',
      })
    }
  }

  const scrollThumbToTop = i => {
    if (thumbsRef.current.length > 0) {
      if (thumbWrap.current.scrollTo) {
        // Edge, IE browsers sometimes complain scrollTo is absent. Hence the check
        thumbWrap.current.scrollTo({
          top:
            thumbsRef.current[i].offsetTop - thumbsRef.current[i].offsetHeight,
          behavior: 'smooth',
        })
      }
    }
  }

  const handleChevronTopClick = indexInView => {
    indexInView !== 0 && scrollTo(indexInView - 1)
    indexInView !== 0 && scrollThumbToTop(indexInView - 1)
    indexInView === 0 && scrollThumbToTop(0)
  }

  const handleChevronBottomClick = indexInView => {
    indexInView < images.length - 1 && scrollTo(indexInView + 1)
    indexInView < images.length - 1 && scrollThumbToTop(indexInView + 1)
    indexInView === images.length - 1 && scrollThumbToTop(images.length - 1)
  }

  // eslint-disable-next-line immutable/no-let
  let imagesWithTrustBadges = carouselImages
  if (mounted) {
    imagesWithTrustBadges = [
      ...imagesWithTrustBadges.slice(0, 1),
      { renderElement: () => <DesktopTrustBadges /> },
      ...imagesWithTrustBadges.slice(1),
    ]
  }

  useEffect(() => {
    const Observer =
      typeof window !== 'undefined' &&
      window.IntersectionObserver &&
      new IntersectionObserver(
        entries => {
          entries.forEach(entry => {
            if (entry.isIntersecting) {
              setIndexInView(parseInt(entry.target.dataset.slide))
            }
          })
        },
        { threshold: 0.6 },
      )
    if (Observer) {
      if (slidesRef.current.length > 0) {
        slidesRef.current.forEach(el => {
          if (el) Observer.observe(el)
        })
      }
    }
    return () => {
      if (Observer)
        slidesRef.current.forEach(el => {
          if (el) Observer.disconnect(el)
        })
    }
  }, [slidesRef, images])

  return (
    <StyledProductCarousel>
      <DesktopCarousel ref={containerRef}>
        <ThumbnailsContainer>
          <ChevronButton
            type='button'
            padding={`0 0 ${desktopVw(18)}`}
            onClick={() => handleChevronTopClick(indexInView)}
            aria-label='scroll product carousel thumbnail up'
          >
            <StyledChevron />
          </ChevronButton>
          <Thumbnails ref={el => (thumbWrap.current = el)}>
            {images.map((image, i) => (
              <Thumbnail
                ref={el => (thumbsRef.current[i] = el)}
                key={image.id}
                active={indexInView === i}
                onClick={() => {
                  scrollTo(i)
                  scrollThumbToTop(i)
                }}
              >
                <Image
                  fluid={image.fluid}
                  style={{
                    position: 'absolute',
                    left: '0',
                    right: '0',
                    bottom: '0',
                    top: '0',
                    zIndex: '-1',
                  }}
                  alt={
                    image.description ||
                    (imageNamePrefix
                      ? `${imageNamePrefix} - ${imageToColorMap[image.id] &&
                          imageToColorMap[image.id].name} - ${i} - Thumbnail`
                      : image.title)
                  }
                />
                {image.video && (
                  <PlayButton>
                    <VideoPlayBack />
                  </PlayButton>
                )}
              </Thumbnail>
            ))}
          </Thumbnails>
          <ChevronButton
            type='button'
            padding={`${desktopVw(18)} 0 0`}
            onClick={() => handleChevronBottomClick(indexInView)}
            aria-label='scroll product carousel thumbnail down'
          >
            <ChevronDown />
          </ChevronButton>
        </ThumbnailsContainer>
        <DesktopSlidesContainer>
          <Breadcrumbs slug={slug} />
          {imagesWithTrustBadges.map((image, i) => (
            <Overflow key={image.id} marginBottom={mounted ? desktopVw(10) : 0}>
              <div ref={el => (slidesRef.current[i] = el)} data-slide={i}>
                {image.video ? (
                  <VideoContainer>
                    <ScaledVideo
                      key={image.video.file.url}
                      url={image.video.file.url}
                      poster={image.fluid.src}
                      contentType={image.video.file.contentType}
                      onVisible={() => {
                        const eventType = EventType.VIDEO_LOAD
                        eventTracker.track(eventType, {
                          productId,
                          color: selectedColor,
                        })
                      }}
                    />
                  </VideoContainer>
                ) : (
                  <StyledProductCarouselSlide
                    slidesRef={slidesRef}
                    index={i}
                    productId={productId}
                    canZoom={!image.renderElement}
                    className={
                      !mounted
                        ? getEagerLoadCarouselClassName(image, image?.bucket)
                        : ''
                    }
                    display={mounted ? 'block' : 'none'}
                  >
                    {image.renderElement ? (
                      image.renderElement()
                    ) : (
                      <ScaledImage
                        backgroundColor={theme.colors.greyLighter}
                        fluid={image.fluid}
                        style={{
                          position: 'absolute',
                          left: '0',
                          right: '0',
                          bottom: '0',
                          top: '0',
                          zIndex: '-1',
                        }}
                        alt={
                          image.description ||
                          (imageNamePrefix
                            ? `${imageNamePrefix} - ${imageToColorMap[
                                image.id
                              ] && imageToColorMap[image.id].name} - ${i}`
                            : image.title)
                        }
                        loading='eager'
                      />
                    )}
                  </StyledProductCarouselSlide>
                )}
              </div>
            </Overflow>
          ))}
        </DesktopSlidesContainer>
      </DesktopCarousel>
      <SidePanelDesktop>
        <StickyKit offsetTop={desktopVw(85)} parentHeight={containerHeight}>
          <SidePanel ref={sidePanelRef}>{children}</SidePanel>
        </StickyKit>
      </SidePanelDesktop>
    </StyledProductCarousel>
  )
}

const StyledProductCarousel = styled.section`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    justify-content: center;
  }
`

const DesktopCarousel = styled.div`
  display: none;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    margin-left: ${desktopVw(80)};
  }
`

const DesktopSlidesContainer = styled.div`
  nav {
    margin-top: -${desktopVw(38)} !important;
    justify-content: flex-start !important;
    padding-left: 0 !important;
  }
  width: ${desktopVw(434)};
`

const ChevronButton = styled.button`
  width: 100%;
  padding: ${props => props.padding};
  cursor: pointer;
`

const StyledChevron = styled(Chevron)`
  width: ${desktopVw(8.5)};
  height: ${desktopVw(5.6)};
`

const ChevronDown = styled(StyledChevron)`
  transform: rotate(180deg);
`

const ThumbnailsContainer = styled.div`
  position: sticky;
  top: ${desktopVw(72)};
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  margin-right: ${desktopVw(80)};
`

const Thumbnails = styled.ul`
  height: 100%;
  max-height: ${desktopVw(625)};
  overflow: auto;
  -ms-overflow-style: none;

  &::-webkit-scrollbar {
    display: none;
  }
`

const Thumbnail = styled.li`
  position: relative;
  width: ${desktopVw(80)};
  height: ${desktopVw(105)};
  margin-bottom: ${desktopVw(20)};
  border: ${props =>
    props.active ? '1px solid black' : '1px solid transparent'};
  cursor: pointer;

  &:last-child {
    margin-bottom: 0;
  }
`

const Overflow = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    overflow: hidden;
    position: relative;
    margin-bottom: ${props => props.marginBottom};

    &:last-child {
      margin: 0;
    }
  }
`

export default ProductCarousel
