import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import ReactCursorPosition from '@appinfini/react-cursor-position'

import { desktopVw } from '../styles/utils'
import eventTracker from '../trackers/EventTracker'
import EventType from '../trackers/eventType'

const StyledSlide = styled.div`
  position: relative;
  height: 526px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(434)};
    height: ${desktopVw(543)};
    transform-origin: center;
    transition: transform 0 linear;
  }
`

const Slide = ({
  children,
  isPositionOutside,
  position,
  elementDimensions,
  slidesRef,
  index,
  productId,
  canZoom,
}) => {
  const [zoom, setZoom] = useState(false)

  useEffect(() => {
    if (isPositionOutside) {
      setZoom(false)
      toggleZoom(index)
    }
  }, [isPositionOutside])

  const toggleZoom = index => {
    if (!zoom && !isPositionOutside && slidesRef.current[index]) {
      slidesRef.current[index].style.transform = `scale(1.5)`
      slidesRef.current[index].style.cursor = `zoom-out`
    } else {
      slidesRef.current[index].style.transform = `scale(1)`
      slidesRef.current[index].style.cursor = canZoom ? `zoom-in` : 'initial'
    }
  }

  return (
    <StyledSlide
      className='product__carousel__slide__styled_slide'
      onClick={() => {
        if (canZoom) {
          setZoom(!zoom)
          toggleZoom(index)
          if (!zoom) {
            eventTracker.trackInternal(EventType.ZOOM_PRODUCT_IMAGE, {
              productId,
            })
            eventTracker.trackGA(EventType.ZOOM_PRODUCT_IMAGE, {})
          }
        }
      }}
      style={
        zoom && !isPositionOutside
          ? {
              transform: `scale(1.5)  translate(${(elementDimensions.width -
                position.x) /
                3}px, ${(elementDimensions.height - position.y) / 3}px)`,
            }
          : {
              transform: `scale(1) translate(0)`,
            }
      }
    >
      {children}
    </StyledSlide>
  )
}

const ProductCarouselSlide = ({
  children,
  slidesRef,
  index,
  productId,
  canZoom,
  className,
}) => {
  return (
    <ReactCursorPosition className={className}>
      <Slide
        slidesRef={slidesRef}
        index={index}
        productId={productId}
        canZoom={canZoom}
      >
        {children}
      </Slide>
    </ReactCursorPosition>
  )
}

export default ProductCarouselSlide
