import React from 'react'
import styled from 'styled-components'

import { desktopVw } from '../styles/utils'

import { getValidColorName } from '../utils'

import ColorOption from './ColorOption'
import { captureWarning } from './Sentry/utils'

const StyledColorSelector = styled.div`
  align-items: center;
  padding: 15px 0;
  border-top: 1px solid ${props => props.theme.colors.greyLighter};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0;
    border-top: none;
    flex-wrap: wrap;
    margin-bottom: ${desktopVw(12)};
  }
`

const ColorLabel = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 15px;
`

const Title = styled.label`
  font-size: 11px;
  margin-right: 20px;
  font-weight: 500;
  text-transform: uppercase;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-right: ${desktopVw(12)};
    font-size: ${desktopVw(11)};
  }
`

const ColorOptions = styled.div`
  display: grid;
  grid-template-columns: var(--gridTemplateColumnsMobile);
  column-gap: 10px;
  row-gap: 10px;
  @media (min-width: ${props =>
      props.theme.breakpoints.tablet}px) and (max-width: 1260px) {
    grid-template-columns: var(--gridTemplateColumnsTablet);
  }
  @media (min-width: ${props => props.theme.breakpoints.desktop}px) {
    grid-template-columns: var(--gridTemplateColumnsDesktop);
  }
`

const Option = styled.div`
  display: flex;
  position: relative;
`

const Label = styled.label`
  display: var(--display);
  flex-shrink: 0;
  transition: width 250ms ease-out;
  white-space: nowrap;
  overflow: hidden;
  color: ${props => props.theme.colors.greyLight};
  font-size: 10px;
  margin-left: 9px;
  font-weight: 500;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 1px 0 0 ${desktopVw(9)};
    font-size: ${desktopVw(10)};
  }
`

const ColorWrapper = styled.div``

const StyledColorOption = styled(ColorOption)`
  width: var(--width) !important;
  height: 28px !important;
  border-radius: 6px !important;
`

const Input = styled.input`
  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
  z-index: 1;
  opacity: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;

  &:checked {
    + ${ColorWrapper} {
      border: ${props => `1px solid ${props.theme.colors.primary}`};
      border-radius: 6px;
      > ${StyledColorOption} {
        margin: 2px 3px;
      }
    }
  }
`

const InputWrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
`

const ProductColorSelector = ({
  colors,
  onChange,
  selected,
  shopifyColors,
  slug,
  showPrice = false,
}) => {
  const selectedColor = colors.find(
    color => getValidColorName(color.name) === selected,
  )

  const { name: selectedColorName } = selectedColor || {}

  return (
    <StyledColorSelector>
      <ColorLabel>
        {slug && slug == 'gift-card' ? (
          <Title>Value</Title>
        ) : (
          <Title>Color</Title>
        )}
        <Label
          htmlFor={selectedColorName}
          style={{
            '--display': selectedColorName === selected ? 'block' : 'none',
          }}
        >
          {selectedColorName}
        </Label>
      </ColorLabel>
      <ColorOptions
        style={{
          '--gridTemplateColumnsMobile': showPrice
            ? 'repeat(5, 1fr)'
            : 'repeat(9, 1fr)',
          '--gridTemplateColumnsTablet': showPrice
            ? 'repeat(4, 1fr)'
            : 'repeat(7, 1fr)',
          '--gridTemplateColumnsDesktop': showPrice
            ? 'repeat(5, 1fr)'
            : 'repeat(10, 1fr)',
        }}
      >
        {colors
          .filter(color => shopifyColors.indexOf(color.name) > -1)
          .map(({ name, ...rest }) => ({
            name: getValidColorName(name),
            ...rest,
          }))
          .map(({ name, hexcode, texture, addBorder, hasText }) => (
            <Option key={`${name}${hexcode}`}>
              <InputWrapper>
                <Input
                  name='Color'
                  aria-label={`select-product-color-${name}-${hexcode}`}
                  id={name}
                  value={name}
                  checked={name === selected}
                  type='radio'
                  onChange={e => onChange(e)}
                />
                <ColorWrapper>
                  <StyledColorOption
                    color={hexcode}
                    name={name}
                    texture={texture}
                    addBorder={addBorder}
                    isPositionInitial={slug === 'gift-card' || hasText}
                    style={{ '--width': showPrice ? '60px' : '28px' }}
                  />
                </ColorWrapper>
              </InputWrapper>
            </Option>
          ))}
      </ColorOptions>
    </StyledColorSelector>
  )
}

export default ProductColorSelector
