import { useState, useEffect } from 'react'
import useShopifyProduct from './useShopifyProduct'
import { decodeIdFromGraphQLResponse } from '../utils'

const usePrice = (
  handle,
  colorVariant,
  staticPrice,
  product,
  productPackVariantMap,
) => {
  const [hasPriceRange, setHasPriceRange] = useState(false)
  const [startingPrice, setStartingPrice] = useState(parseFloat(staticPrice))
  const [variantStartingPrice, setVariantStartingPrice] = useState()

  useEffect(() => {
    if (!product) return

    const { minVariantPrice, maxVariantPrice } = product.priceRange
    setStartingPrice(parseFloat(minVariantPrice.amount))

    const groupBuyOption = product.options.filter(
      ({ name }) => name === 'GB',
    )[0]

    if (groupBuyOption) return

    if (
      parseFloat(minVariantPrice.amount) !== parseFloat(maxVariantPrice.amount)
    ) {
      setHasPriceRange(true)
    } else {
      setHasPriceRange(false)
    }
  }, [product])

  useEffect(() => {
    if (!product || !colorVariant) return

    const {
      variants: { edges },
    } = product

    const colorVariants = edges.filter(({ node }) =>
      node.title.includes(colorVariant),
    )

    const prices = colorVariants.map(({ node }) => {
      const variantId = decodeIdFromGraphQLResponse(node.id)
      if (productPackVariantMap?.[variantId])
        return (
          parseFloat(node.priceV2.amount) /
          productPackVariantMap?.[variantId]?.packQuantity
        )
      return parseFloat(node.priceV2.amount)
    })

    const startingPrice = Math.min(...prices)

    setVariantStartingPrice(startingPrice)
  }, [product, colorVariant])

  return { hasPriceRange, startingPrice, variantStartingPrice }
}

export default usePrice
